@font-face {
  font-family: "Quicksand";
  src: url(/dist/fonts/Quicksand-Regular.ttf);
}
$font-size-base: 16px;
$font-size-xs: 14px;
$font-size-sm: 18px;
$font-size-md: 24px;
$font-size-lg: 32px;
$font-size-xl: 60px;

$font-weight-regular: 400;
$font-weight-bold: 700;
$font-weight-bolder: 900;

h1 {
  font-size: $font-size-xl;
  font-weight: $font-weight-bolder;
}

h2 {
  font-size: $font-size-lg;
  font-weight: $font-weight-bolder;
}

h3 {
  font-size: $font-size-md;
}

p {
  font-size: $font-size-base;
}

.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.text-left {
  text-align: left;
}
